@import url('https://fonts.googleapis.com/css2?family=Muli:wght@400;700&display=swap');

* {
  box-sizing: border-box;
}

body {
  font-family: 'Muli', 'Sans-serif';
  display: flex;
  align-items: center;
  height: 100vh;
  overflow: hidden;
  margin: 0;
}

.card-text {
  font-size: 24px;
  color: white;
  position: relative;
  top: 70vh;
  left: 3vh;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.4s;
  user-select: none;
}

.card.card.active .card-text{
  opacity: 1;
}


.card {
  flex: 0.5;
  height: 80vh;
  border-radius: 50px;
  background-position: center;
  background-size: cover;
  cursor: pointer;
  transition: flex 0.7s ease-in;
  margin: 0 10px;
  will-change: flex;
}

.card.active {
  flex: 5;
}

.cards {
  display: flex;
  width: 90vw;
  margin: 0 5vw;
}
